<<template>
  <div>
    <div class="personinfoBody">
      <div class="user-header">
        <a class="nav-item1" @click="personBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
        <a class="personinfo">个人信息</a>
        <a class="save" @click="save">保存</a>
      </div>
      <div class="personMain">
        <div class="personMainT">
          <a>头像</a>
          <img :src="userPath"/>
        </div>
        <div class="personMainC">
          <ul>
            <li @click="changName">
              <a>昵称</a>
              <span v-show="userNameShow">{{userName}}</span>
              <input id="CName" v-on:blur="loseName" v-show="changNameShow" type="text" v-model="userName" placeholder="请输入昵称"/>
            </li>
            <li><a>手机</a><span @click="bandMobile">{{mobile}}</span></li>
            <li @click="changBirthday">
              <a>生日</a>
              <span v-show="birthdayShow">{{birthday}}</span>
              <span v-show="changbirthdayShow">
                  <input id="year" v-on:blur="loseyear" v-model="y" class="sbir" type="number" placeholder="年" maxlength="4"/>/
                  <input id="mounth" v-on:blur="losemounth" v-model="m" class="sbir" type="number" placeholder="月" maxlength="2"/>/
                  <input id="day" class="sbir" v-on:blur="loseBirthday" v-model="dd" type="number" placeholder="日"/>
                </span>
            </li>
            <li @click="sexchange" class="sexLi">
              <a>性别</a>
              <span v-show="sexShow">{{sex}}</span>
              <div v-show="selShow" id="sel">
                <input type="text" value="请选择" disabled>
                <input class="male" type="text" value="男" readonly="true" @click="changsel1"/>
                <input class="female" type="text" value="女" readonly="true" @click="changsel2"/>
              </div>
            </li>
            <li @click="addressMana"><a>地址</a><img src="../../assets/images/thebar/gc_28.png"/></li>
          </ul>
        </div>
      </div>
      <div class="editAvatar" v-show="editAvatarShow" @click="editAvatarHide">
        <div class="editBox" @click.stop>
          <a>拍照</a>
          <a>打开相册</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import defalut from '@/assets/images/home/defalut.png'
  var urlhost = process.env.API_AJAX;
  function getCookie(name) {
    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  }
  function setCookie(name, value, days) {
    var d = new Date;
    d.setTime(d.getTime() + 24*60*60*1000*days);
    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  }
  export default {
    data() {
      return {
        editAvatarShow:false,
        userPath:'',
        mobile:'',
        userName:'',
        changNameShow:false,
        userNameShow:true,
        birthday:'',
        sex:'',
        changbirthdayShow:false,
        birthdayShow:true,
        sexShow:true,
        selShow:false,
        defalut:defalut,
        selected:'',
        y:'',
        m:"",
        dd:""
      }
    },
    components: {
    },
    mounted:function () {
      var _this= this;
      var format = function (timestamp) {
        var date = new Date(timestamp * 1000);
        var Y = date.getFullYear() + '/';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
        var D = date.getDate() + ' ';
        return Y+M+D;
      }
      var userId = getCookie('userId');
      $.ajax({
        type: 'GET',
        url: urlhost+"usercenter/userinfo?user_id="+userId,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          // console.log(response);
          var data = JSON.parse(response).message;
          _this.userName = data.nickname;
          _this.userPath = data.img_url;
          if(data.mobile == null || data.mobile == ''){
            _this.mobile = "未绑定";
          }else {
            _this.mobile = data.mobile;
          }
          if(data.birthday == null || data.birthday == ""){
            _this.birthday = '未知'
          }else{
            _this.birthday = format(data.birthday);
            var sliceday = format(data.birthday).split("/");
            _this.y = sliceday[0];
            _this.m = sliceday[1];
            _this.dd = sliceday[2].replace(/(^\s*)|(\s*$)/g, "");
            // console.log(_this.dd)
            _this.birthday = _this.y + "/" + _this.m + "/" + _this.dd;
          }
          let sex = data.sex;
          if(sex == 0){
            _this.sex = '请选择'
          }else{
            switch(sex){
              case 1:
                _this.sex = "男"
                break;
              case 2:
                _this.sex = "女"
                break;
            }
          }


          var heading = data.img_url;
          if(heading == null){
            _this.userPath = _this.defalut
          }
        },
      });


    },
    methods:{
      save:function () {
        var _this = this;
        var token = getCookie("wawajitoken");
        var userid = getCookie('userId');
        var name = _this.userName;
        var sex = 0;
        if(_this.sex == "男"){
          sex = 1;
        }else if(_this.sex == "女"){
          sex = 2;
        }

        if(_this.y == "" || _this.m == "" || _this.dd == "" || name == '' || sex == ''){
          _this.$layer.msg("编辑的内容不能为空!")
        }else{
          $.ajax({
            type: 'POST',
            url: urlhost+"usercenter/useredit",
            data$Type: "jsonp",
            data: {
              'nickname': name,
              "birthday":_this.birthday,
              "sex":sex,
              "user_id":userid,
              "token":token,
            },
            success: function (response) {
              console.log(response);
              var msg = JSON.parse(response);
              if(msg.status === 0){
                _this.$layer.msg(msg.message)
              }else if(msg.status === 1){
                _this.$layer.msg(msg.message)
                _this.$router.push({path:"/usercenter/userInfo"})
              }
            },
          });
        }
      },
      bandMobile:function(){
        this.$router.push({path:"/visit/bind"})
      },
      sexchange:function () {
        this.sexShow = !this.sexShow;
        this.selShow = !this.selShow;
      },
      changsel1:function () {
        this.sex =  $("#sel>.male").val();

      },
      changsel2:function () {
        this.sex =  $("#sel>.female").val();

      },
      changName:function () {
        this.changNameShow = true;
        this.userNameShow = false;
        $("#CName").focus();
      },
      changBirthday:function () {
        var _this = this;
        this.changbirthdayShow = true;
        this.birthdayShow = false;
        _this.y = $("#year").val();
        _this.m = $("#mounth").val() ;
        _this.dd = $("#day").val();
        console.log(_this.dd)
      },
      loseyear:function () {
        var y = $("#year").val();
        if(y){
          this.y = y;
        }else{
          this.$layer.msg('年不能为空')
        }
      },
      losemounth:function () {
        var m = $("#mounth").val();
        if(m){
          this.m = m;
        }else{
          this.$layer.msg('月不能为空')
        }
      },
      loseBirthday:function () {
        var d = $("#day").val();
        if(d){
          this.d = d;
          this.birthday = this.y + "/" + this.m + "/" + this.d;
          this.changbirthdayShow = false;
          this.birthdayShow = true;
        }else{
          this.$layer.msg('日不能为空')
        }
      },
      loseName:function () {
        var _this= this;
        var changName = $("#CName").val();
        if(changName){
          _this.userName = changName;
          _this.changNameShow = false;
          _this.userNameShow = true;
        }else{
          this.$layer.msg("昵称不能空")
        }

      },
      addressMana:function () {
        this.$router.push({path:"/usercenter/address"})
      },
      personBack:function () {
        this.$router.go(-1);
      },

      editAvatar:function () {
        this.editAvatarShow = true;
      },
      editAvatarHide:function () {
        this.editAvatarShow = false;
      },
      close:function () {
        var userAgent = navigator.userAgent;
        window.history.back();
      },
    }
  }
</script>
<style lang="scss" scoped>
  /*个人信息*/
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .personinfoBody{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    background-color: #f2f2f2;
    .user-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      justify-content: space-between;
      align-items: center;
      z-index: 999;
      border-bottom: 1px solid #d8d8d8;
      .nav-item1{
        /*position: absolute;*/
        /*left: 0.2rem;*/
        margin-left: n(23);
        .game_1{
          width: n(50);
          height: n(50);
        }
      }
      .personinfo{
        font-size: 14px;
        font-weight: bold;
      }
      .save{
        margin-right: n(23);
      }
    }
    .personMain{
      margin-top: n(80);
      width: 100%;
      .personMainT{
        margin-top: 10px;
        width: 100%;
        display: inline-flex;
        align-items: center;
        justify-content: space-between;
        padding-left: n(70);
        padding-right: n(40);
        height: n(90);
        background: #ffffff;
        border-top: 1px solid #dddddd;
        border-bottom: 1px solid #dddddd;

        img{
          width: n(75);
          height: n(75);
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          border-radius: 50%;
        }
      }

      .personMainC{
        width: 100%;
        margin-top: 10px;
        ul{
          background: #ffffff;
          padding-left: n(70);
          li{
            width: 100%;
            height: n(90);
            background: #ffffff;
            display: inline-flex;
            align-items: center;
            justify-content: space-between;

            padding-right: n(40);
            border-bottom: 1px solid #e4ece1;
            img{
              width: n(20);
              height: n(40);
            }
            input{
              width: n(150);
              height: n(40);
            }
            .sbir{
              width: n(55);
              text-align: right;
            }
          }
        }
      }
      .exit{
        letter-spacing: 2px;
        margin-top: n(30);
        width: 100%;
        height: n(90);
        text-align: center;
        line-height: n(90);
        background: #ffffff;
        font-size: n(28);
      }
    }
    .editAvatar{
      position: fixed;
      z-index: 9998;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, .5);
      display: table;
      transition: opacity .3s ease;
      .editBox{
        width: 100%;
        height: n(254);
        background: #ffffff;
        position: absolute;
        bottom: 0;
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        a{
          width: n(340);
          height: n(60);
          background: #fef102;
          text-align: center;
          line-height: n(60);
          display: block;
          margin-bottom: 10px;
          -webkit-border-radius: 5px;
          -moz-border-radius: 5px;
          border-radius: 5px;
        }
      }
    }

  }
  .sexLi{
    position: relative;
    #sel{
      position: absolute;
      right: 15px;
      top: 50%;
      width: 100px;
      height: auto;
      border: 1px solid #ccc;
      input{
        height: 20px;

        width: 100%;
        text-align: center;
      }
      input:hover{
        background: #ededed;
      }
    }
  }
</style>




